import {SHOW_SIGNUP,SHOW_SIGNIN,CLOSE_DIALOG} from './actionTypes'
export const showSignUp=()=>({
    type:SHOW_SIGNUP
})
export const showSignIn=()=>({
    type:SHOW_SIGNIN
})
export const closeDialog=()=>({
    type:CLOSE_DIALOG
})



